.input {
    display: flex;
    width: 95%;
    position: relative;
    align-items: center;
}

.container {
    display: flex;
    width: 95%;
    margin-top:10px;
    align-items: flex-start;
    justify-content: space-between;
}

.input-box {
    width: 100%;
    border-radius: 50px;
    padding: 20px 30px;
    font-size: 25px;
    border: none;
    transition: 0.2s;
    box-shadow: inset 0 0 5px black;
}

.input-box:focus {
    box-shadow: 0 0 10px 1000px rgba(0,0,0,0.4);
    outline: none;
}

.input-submit {
    position: absolute;
    width: 50px;
    height: 50px;
    margin: 12px;
    border-radius: 50px;
    right: 0;
    border: none;
    font-size: 15px;
    background-color: #2f74c0;
    color: white;
    transition: 0.2s all;
    box-shadow: 0 0 10px black;
}

.input-submit:hover {
    background-color: #388ae2;
}

.input-submit:active {
    transform: scale(0.8);
    box-shadow: 0 0 5px black;
}

.todos {
    display: flex;
    width: 47.5%;
    flex-direction: column;
    padding: 15px;
    margin: 0 5px;
    border-radius: 5px;
    background-color: rgb(50,195,205);
}

.todos-heading {
    font-size: 30px;
    color: white;
}

.todos-single {
    display: flex;
    /* width: 29.5%; */
    border-radius: 5px;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    margin-top: 15px;
    background-color: yellow;
    /* background-image: url("https://www.freepik.com/free-vector/yellow-background-with-halftone-lines-design_12572926.htm#query=background%20images&position=2&from_view=search"); */
    transition: 0.2s;
}

.todos-single:hover {
    box-shadow: 0 0 5px black;
    transform: scale(1.03)
}

.todos-single-text {
    /* flex: 1; */
    width: 70%;
    padding: 5px;
    border: none;
    font-size: 20px;
}

.icon {
    margin-left: 10px;
    font-size: 25px;
    cursor: pointer;
}

.remove {
    background-color: rgb(235,103,80);
}

@media (max-width:600px){
    .icon {
        margin-left: 10px;
        font-size: 15px;
        cursor: pointer;
    }
}

@media (max-width:700px){
    .input {
        width: 95%;
    }
    .todos{
        width: 95%;
        margin-bottom: 10px;
    }
    .container {
        width:95%;
        flex-direction: column;
    }
}

